.frequently{
    margin: 60px 10%;
}

.frequently details summary{
    font-family: "NotoSansCJKKr Bold";
    font-size: 25px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.6;
    letter-spacing: normal;
    text-align: left;
    color: #000000;
    cursor: pointer;
    -ms-user-select:none;
    -moz-user-select:none;
    -webkit-user-select:none;
    user-select:none;
    outline: none;
    margin-bottom: 50px;
}

.frequently details summary::-webkit-details-marker {
    display: none;
}

.frequently details summary:after {

    content: url('https://user-images.githubusercontent.com/48242362/75758108-e358e780-5d76-11ea-877b-3291055378de.png');
    float: right;
}

.frequently details[open] summary:after {
    display: inline;
    content: url('https://user-images.githubusercontent.com/48242362/75758104-e2c05100-5d76-11ea-8e72-8a4f0947813f.png');
}

.frequently details span{
    font-family: "NotoSansCJKKr Regular";
    font-size: 25px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.6;
    letter-spacing: normal;
    text-align: left;
    color: #707070;
}

.frequently hr{
    border: 0;
    height: 0.8px;
    margin-top: 60px;
    background: #707070;
}

@media screen and (max-width: 1100px) {
    
    .frequently details summary{
        font-size: 15px;
    }

    .frequently details summary:after {
        content: url('https://user-images.githubusercontent.com/48242362/76760168-7f034280-67d0-11ea-9851-bfa818157e5e.png');
        float: right;
    }
    
    .frequently details[open] summary:after {
        content: url('https://user-images.githubusercontent.com/48242362/76760207-94786c80-67d0-11ea-9ccc-b78726af2a19.png');
    }

    .frequently details span{
        font-size: 15px;
    }
}