.member{
    width: 300px;
    margin-bottom: 40px;
}

.member a:hover, .member a:active, .member a, .member a:visited{
    text-decoration: none;
    color:black;
}

.member .mem-img{
    width: 300px;
    height: auto !important;
    margin-bottom: 10px;
}

.member .mem-name{
    font-family: "NotoSansCJKKr Bold";
    font-size: 25px;
    font-weight: bold;
    text-align: left;
    color: #000000;
}

.member .mem-major{
    font-family: "NotoSansCJKKr Medium";
    font-size: 15px;
    font-weight: 500;
    text-align: left;
    color: #707070;
    margin-bottom: 40px;
}

.member:last-child() .mem-major{
    margin-bottom: 0;
}

@media screen and (max-width: 1100px) {

    .member{
        width: 105px;
        margin-bottom: 20px;
    }

    .member .mem-img{
        width: 105px;
    }

    .member .mem-name{
        font-size: 0.9rem;
    }

    .member .mem-major{
        font-size: 0.7rem;
        margin-bottom: 40px;
    }
}