.about{
    max-width: 100%;
}

.about .about-intro{
    margin: 0 10%;
    text-align: center;
}

.about h1{
    font-size: 38px;
    text-align: center;
    color: #000000;
    margin: 60px 0 60px;
}

.about .about-intro h3{
    font-size: 25px;
    text-align: center;
    color: #000000;
    margin-bottom: 60px;
}

.about .about-intro div{
    font-size: 22px;
    text-align: left;
    color: #707070;
    margin-bottom: 60px;
}

.about .about-intro table{
    margin: 0 auto 160px;
}

.about .about-intro table td{
    height: 24px;
    font-size: 20px;
}

.about .about-intro table .temp{
    width: 28%;
    color: #000;
    text-align: left;
    font-weight: bold;
}

.about .about-intro table .mar-temp{
    height: 40px;
}

@media screen and (max-width: 1100px) {
    
    .about h1{
        font-size: 2rem;
        margin: 50px 0 50px;
    }

    .about .about-intro h3{
        font-size: 1rem;
        margin-bottom: 50px;
    }

    .about .about-intro div{
        font-size: 0.9rem;
        margin-bottom: 50px;
    }
    
    .about .about-intro table td{
        font-size: 0.8rem;
    }

    .about .about-intro table .temp{
        text-align: center;
    }
}